<template>
  <div id="app">
    <div class="banner">
      <img
        src="https://vuejs.org/images/logo.png"
        width="100"
        alt="vue"
        class="logo"
      />
      <h1>Welcome to timed</h1>
    </div>
    <div class="bottom">
      Current labor: <input v-model="labor" type="number"/>
    </div>
    <div class="calculations">
      <p>Minutes to max: {{ minutesToOne }}</p>
      <p>Time @ 5k: {{ timeAt5k }}</p>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'app',
    data () {
      return {
        labor: 0
      }
    },
    computed: {
      minutesToOne () {
        return (((5000 - parseInt(this.labor, 10)) / 50 ) * 10)
      },
      timeAt5k () {
        const now = new Date().getTime()
        return new Date(now + this.minutesToOne * 60000)
      }
    }
  }
</script>

<!-- CSS libraries -->
<style src="normalize.css/normalize.css"></style>

<!-- Global CSS -->
<style>
  code {
    font-family: Menlo, Monaco, Lucida Console, Liberation Mono, DejaVu Sans Mono, Bitstream Vera Sans Mono, Courier New, monospace, serif;
    font-size: 0.9em;
    white-space: pre-wrap;
    color: #2c3e50;
  }

  code::before, code::after {
    content: '`';
  }
</style>

<!-- Scoped component css -->
<!-- It only affect current component -->
<style scoped>
  #app {
    text-align: center;
  }

  #app h1 {
    color: #2c3e50;
    font-weight: 300;
    margin: 0;
  }

  .banner {
    height: 200px;
    background-color: #f6f6f6;
    padding: 50px 10px;
  }

  .bottom {
    padding: 80px 10px;
    font-size: 24px;
    font-weight: 300;
  }

  .fade {
    font-size: 14px;
  }

  .logo {
    animation: spin 4s 1s infinite linear
  }

  @keyframes spin {
    from {transform:rotate(0deg);}
    to {transform:rotate(360deg);}
  }
</style>



